import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { AuthenticationConstants } from '../app.constants';
import { LoginService } from 'src/shared/services/login.service';
import { StatusBar } from '@capacitor/status-bar';
import { setStyleStatusBar } from 'src/shared/utils/function.utils';

@Component({
  selector: 'app-register-home',
  templateUrl: './register-home.component.html',
  styleUrls: ['./register-home.component.scss'],
})
export class RegisterHomeComponent implements OnInit {
  authProviders = AuthenticationConstants.authProviders
  constructor(
    private readonly spinner: NgxSpinnerService,
    private readonly loginService: LoginService,
    private router: Router,
  ) {
  }

  ngOnInit() { }

  ionViewDidEnter() {
    setStyleStatusBar("dark")
    this.spinner.hide('appSpinner')
  }
  onRegister(providerName: string) {
    this.loginService.handleProvider(providerName);
  }



  onRedirectToLogin() {
    this.router.navigateByUrl('/login', { replaceUrl: true })
  }

}
